<template>
  <div>
    <Page>
      <b-form>
        <b-form-row>
          <b-form-group label="Data Início" class="col-md-auto">
            <date-picker
              v-model="time1"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Data Final" class="col-md-auto">
            <date-picker
              v-model="time2"
              format="DD-MM-YYYY HH:mm:ss"
              class="mt-1"
              type="datetime"
            ></date-picker>
          </b-form-group>
          <b-form-group label="Telefone" class="col-md-auto">
            <the-mask
              v-model="tel"
              class="form-control"
              :mask="['###', '(##) #####-####', '(##) ####-####']"
            />
          </b-form-group>
        </b-form-row>
        <b-form-row>
          <b-form-group label-align-md="right" class="col-md-auto">
            <b-check v-model="noanswered">Não Atendidas</b-check>
          </b-form-group>
          <b-form-group label-align-md="right" class="col-md-auto">
            <b-check v-model="busy">Ocupadas</b-check>
          </b-form-group>
        </b-form-row>
        <b-form-row>
          <ladda-btn
            :loading="loading"
            data-style="expand-left"
            class="btn btn-success"
            @click.native="getActiveLayer"
            >Visualizar</ladda-btn
          >
        </b-form-row>
      </b-form>
    </Page>
    <Page :hidden="table.length == 0">
      <Table :tableData="table" :filename="fileName" :header="header" :legend="legend" />
    </Page>
  </div>
</template>
<script>
import Page from "@/components/Page";
import DatePicker from "vue2-datepicker";
import "@/vendor/libs/vue2-datepicker/locale/pt-br";
import moment from "moment";
import LaddaBtn from "@/vendor/libs/ladda/Ladda";
import Table from "@/components/Table";
import axios from "axios";
export default {
  name: "home",
  components: {
    Page,
    DatePicker,
    LaddaBtn,
    Table,
  },
  metaInfo: {
    title: "Relatório de Chamadas Ativas por Telefone",
  },
  data: () => ({
    loading: false,
    phoneMask0: [
      "(",
      /[1-9]/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
    ],
    phoneMask1: [
      "(",
      /[1-9]/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
    ],
    phoneMask2: [
      "(",
      /[1-9]/,
      /\d/,
      ")",
      " ",
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
      /\d/,
    ],
    phone: "",
    time1: moment().hour(0).minute(0).second(0).toDate(),
    time2: moment().hour(23).minute(59).second(59).toDate(),
    noanswered: false,
    busy: false,
    agents: [],
    tel: "",
    table: [],
    header: [],
    legend: [],
    fileName: "",
  }),
  mounted() {
    this.$store.commit("setTitle", "Relatório de Chamadas Ativas por Telefone");
    this.getAgents();
  },
  methods: {
    getAgents() {
      axios
        .post(`${location.origin}/reportsbackend/agents_callcenter_ramal.php`)
        .then((data) => {
          this.agents = data.data;
        })
        .catch((err) => console.log(err))
        .finally((x) => {
          this.loading = false;
        });
    },
    getActiveLayer() {
      this.loading = true;
      const params = new URLSearchParams();
      params.append("time1", moment(this.time1).format("YYYY-MM-DD HH:mm:ss"));
      params.append("time2", moment(this.time2).format("YYYY-MM-DD HH:mm:ss"));
      params.append("noanswered", this.noanswered ? "1" : "0");
      params.append("busy", this.busy ? "1" : "0");
      params.append("tel", this.tel);
      axios
        .post(`${location.origin}/reportsbackend/report_active_layer.php`, params)
        .then((data) => {
          if (data.data.length === 0) {
            this.$bvToast.toast("Nenhum registro encontrado", {
              title: "Tecnomafer Report",
              autoHideDelay: 5000,
              appendToast: true,
              toastClass: "bs4-toast bg-danger",
            });
          }
          const tempTable = data.data;
          tempTable.forEach((x) => {
            const agentOri = this.agents.find((y) => y.number === x.Origem);
            const agentDest = this.agents.find((y) => y.number === x.Destino);
            if (agentOri !== undefined) {
              x.Origem = agentOri.name;
            }
            if (agentDest !== undefined) {
              x.Destino = agentDest.name;
            }
          });

          this.table = tempTable;
          this.updateHeader();
        })
        .catch((err) => console.log(err))
        .finally((x) => {
          this.loading = false;
        });
    },
    updateHeader() {
      this.header = [];
      this.header.push("<h4>Relatório de Chamadas Ativas por Telefone</h4>");
      this.header.push(
        `Período ${moment(this.time1).format("DD/MM/YYYY")} à ${moment(this.time2).format(
          "DD/MM/YYYY"
        )} / Hora à ${moment(this.time1).format("HH:mm:ss")} até ${moment(
          this.time2
        ).format("HH:mm:ss")}`
      );
      this.header.push(" ");

      this.legend = [
        "<h5>Legenda:</h5>",
        "<b>Ramal</b> - Ramal do do agente;",
        "<b>Agente</b> - Telefone do agente;",
        "<b>Destino</b> - Telefone de destino;",
        "<b>Duração</b> - Tempo da duração da chamada",
        "<b>Evento</b> - Evento da chamada;",
        `<b>Emitido em:</b> ${moment().format("DD/MM/YYYY HH:mm:ss")}`,
      ];

      this.fileName = `Rel_agente_desempenho ${moment().format("DD_MM_YYYY_HH_mm_ss")}`;
    },
  },
};
</script>
